.video {
  background-color: $color-default-white;
  border-bottom-left-radius: 24px;
  border-bottom-right-radius: 24px;

  @include vp--767 {
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
  }
}


.video__video-box {
  img {
    width: 100%;
  }
}

.video__video-loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-bottom: 56.3%;
  background-color: $color-blue-charcoal;
  transform: translateZ(0);
}

.video__lock {
  position: absolute;
  width: 100%;
  height: 100%;
  align-items: center;
  display: flex;
  justify-content: center;

  background: rgba($color-default-black, 0.4);

  &--next {
    background: rgba($color-default-black, 0.8);

    @include vp--767 {
      display: none;
    }
  }

  svg {
    fill: none;
    justify-self: center;
  }

  @include vp--767 {
    row-gap: 0;

    svg {
      width: 80px;
      height: 80px;
    }
  }
}

.video__lock-text {
  display: grid;
  grid-template-columns: fit-content;
  font-size: 17px;
  line-height: 23px;
  color: $color-default-white;
  row-gap: 16px;
  text-align: center;
  max-width: 264px;

  @include vp--1023 {
    padding-bottom: 40px;
  }

  @include vp--767 {
    font-size: 13px;
    line-height: 18px;
    row-gap: 0;
    max-width: 198px;
    padding-bottom: 5px;
  }
}

.video__description {
  padding-top: 40px;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 40px;
  overflow: hidden;

  @include vp--767 {
    padding-top: 32px;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 32px;
  }
}


.video__description-section-container {

}

.video__description-section {
  margin-top: 40px;

  &:first-child {
    margin-top: 0;
  }

  @include vp--767 {
    margin-top: 32px;
  }
}





.video__description-section-title {
  margin-bottom: 24px;
  font-size: 21px;
  line-height: 1.15em;
  font-weight: 500;
  margin-top: 0;
  margin-bottom: 24px;
}


.video__description-chips-list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  display: flex;
  column-gap: 16px;
}


.video__descritption-section-text {
  max-width: 600px;
  margin-top: 16px;
  font-size: 18px;
  line-height: 1.5em;
  font-weight: 500;
  color: $color-night-rider;
  margin-bottom: 0;
}


.video__description-header {
  margin-bottom: 40px;

  @include vp--767 {
    margin-bottom: 32px;
  }
}

.video__description-header--with-comments {
  margin-bottom: 28px;

  @include vp--1023 {
    margin-bottom: 30px;
  }

  @include vp--767 {
    margin-bottom: 38px;
  }

  .video__description-header-title {

    @include vp--767 {
      width: 100%;
      padding-right: 42px;
      font-size: 23px;
    }
  }

  .video__description-header-wrapper {
    @include vp--767 {
      margin-bottom: 34px;
    }
  }
}

.video__description-header-inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 1px solid $color-silver;
  padding-bottom: 16px;
  // margin-bottom: 24px;
  //width: 920px;

  // &--active {
  //   margin-bottom: 19px;

  //   @include vp--1023 {
  //     margin-bottom: 24px;
  //   }
  // }

  @include vp--767 {
    display: block;
  }
}

.video__description-header-title {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 25px;
  line-height: 115%;

  @include vp--1023 {
    max-width: 420px;
    font-size: 25px;
  }

  @include vp--767 {
    width: 100%;
    font-size: 21px;
    margin-bottom: 24px;
    line-height: 1.15em;
  }
}

.video__description-header-sub-title {
  font-weight: 500;
  font-size: 21px;
  line-height: 115%;
  margin: 0;
  margin-bottom: 17px;
  padding: 0;

  @include vp--767 {
    font-size: 19px;
  }
}

.video__description-header-text {
  width: 520px;
  font-size: 15px;
  line-height: 20px;
  font-weight: 500;

  @include vp--1023 {
    max-width: 520px;
    width: 100%;
  }

  @include vp--767 {
    font-size: 15px;
    line-height: 20px;
    padding: 0 2px;
    padding-right: 5px;
  }
}

.video__description-header--wide {
  max-width: 608px;
  width: auto;
}

.video__description-header-like-text {
  font-weight: 500;
}

.video__description-header-desc {
  margin-bottom: 35px;

  p {
    margin: 0;
    margin-bottom: 1em;
    padding: 0;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
